<template>
  <div class="ration-model-set">
    <div class="topTitle">
      <span class="topTitle-span1">模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">{{typeNm}}洗钱风险模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">定量模型设置</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
          <div class="button-content">
            <Button type="primary" @click="operate('6')" v-authcontrol="{menuCode:typeNm == '业务'?'111001002010':'111002002010'}">
              <i class="ivu-icon ivu-icon-md-add"></i>新增模型指标项
            </Button>
            <Button type="primary" @click="importFn" v-authcontrol="{menuCode:typeNm == '业务'?'111001002011':'111002002011'}">
              <i class="ivu-icon ivu-icon-md-arrow-down"></i>导入
            </Button>
            <Button type="primary"  @click="exportFn" v-authcontrol="{menuCode:typeNm == '业务'?'111001002012':'111002002012'}">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item, index) in tableHeader" :key="index">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item, index) in tableBody">
                  <tr :class="item.id==id?'bgColor':''" :key="index" @click="updateTableBody2(item)">
                    <td>{{index+1}}</td>
                    <td>{{item.modelCode}}</td>
                    <td>{{item.modelName}}</td>
                    <td>{{item.version}}</td>
                    <td>{{item.scope}}</td>
                    <td > 
                        <i-switch v-model="item.flag" true-value="1" false-value="0" @on-change="changeUseSts(item,index)" v-authcontrol="{menuCode:typeNm == '业务'?'111001002003':'111002002003'}">
                            <span slot="open">启用</span>
                            <span slot="close">停用</span>
                        </i-switch>
                    </td>
                    <td>{{item.remark}}</td>
                    <td>{{item.updateTime}}</td>
                    <td>{{typeNm == "业务"?item.username:item.updater}}</td>
                    <td>
                      <span class="spanBox" @click.stop="operate('1', item)" v-authcontrol="{menuCode:typeNm == '业务'?'111001002001':'111002002001'}">修改</span>
                      <span class="spanBox" @click.stop="operate('2', item)" v-authcontrol="{menuCode:typeNm == '业务'?'111001002004':'111002002004'}">发布版本</span>
                      <span class="spanBox" @click.stop="operate('3', item)" v-authcontrol="{menuCode:typeNm == '业务'?'111001002002':'111002002002'}">历史版本</span>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
            <table class="tableBox mt_20">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader2" :key="index">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item, index) in tableBody2">
                  <tr :key="index">
                    <td>{{item.indicatorCode}}</td>
                    <td >
                      <el-popover
                            placement="top-start"
                            title
                            width="200"
                            trigger="hover"
                            :content="item.indicatorName"
                            >
                              <span
                                slot="reference"
                                class="textOverflow"
                              >{{item.indicatorName}}</span>
                         </el-popover>
                        
                    </td>
                    <td>{{item.formula}}</td>
                    <td>{{item.weight}}</td>
                    <td>
                        <i-switch v-model="item.flag" true-value="1" false-value="0" @on-change="changeUseSts2(item, index)" v-authcontrol="{menuCode:typeNm == '业务'?'111001002005':'111002002005'}">
                            <span slot="open">启用</span>
                            <span slot="close">停用</span>
                        </i-switch>
                    </td>
                    <td>
                        <el-popover
                            placement="top-start"
                            title
                            width="200"
                            trigger="hover"
                            :content="item.updateTime"
                            >
                              <span
                                slot="reference"
                                class="textOverflow"
                              >{{item.updateTime}}</span>
                         </el-popover>
                    </td>
                    <td>{{typeNm == "业务"?item.updateName:item.updater}}</td>
                    <td>
                      <!-- <span class="spanBox" @click="operate('4', item)">分析</span> -->
                      <span class="spanBox" @click="operate('5', item)" v-if="item.hasSon == '0'">详情</span>
                      <span class="spanBox" @click="operate('7', item)" v-authcontrol="{menuCode:typeNm == '业务'?'111001002006':'111002002006'}">修改</span>
                      <!-- <span class="spanBox" @click="operate('8', item)">删除</span> -->
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showAddModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '80%'}"
    >
      <component :is="tabview" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" width="80%" v-if="showAddModal"></component>
      <div slot="footer"></div>
    </Modal>
    <Modal :mask-closable="false"   v-model="modal1" :footer-hide="true" width="360">
          <Upload
                  ref="upload"
                  type="drag"
                  :format="['xls','xlsx']"
                  :data="uploadParams"
                  :on-format-error="handleFormatError"
                  :on-success="handleSuccess"
                  :on-error="handleError"
                  :action="axios.defaults.baseURL+'/excelTooler/doImportExcel'"
                >
                  <div style="padding: 20px 0">
                    <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                    <p>请上传</p>
                  </div>
        </Upload>
    </Modal>
  </div>
</template>
<script>
//table1部分
import rationModelModify from "../common/rationModelSet/rationModelModify";//修改
import historyVersion from "../common/rationModelSet/historyVersion";//历史版本
//table2部分
import tab2Analyze from "../common/rationModelSet/tab2Analyze";//分析
import tab2Detail from "../common/rationModelSet/tab2Detail";//详情
import tab2Add from "../common/rationModelSet/tab2Add";//新增 修改

export default {
  components: {
    historyVersion,
    rationModelModify,
    tab2Analyze,
    tab2Detail,
    tab2Add,
  },
  data() {
    return {
      tabview:'rationModelModify',
      modal1: false,
      uploadParams:{
          type: "",
          batchId: "",
          modelId: "",
          modelName: "",
      },
      showAddModal: false,
      id: "",
      stopBubb: false,
      dataObj:{
        /**
         * table1：1修改，2发布版本，3历史版本，
         * table2: 4分析，5详情，6新增，7修改，8删除
        */
        data:{},
      },
      modelTitle: "新增",
      selectTable1Url: "",
      selectTable2Url: "",
      typeNm: "业务",
      tableHeader: [
        { name: "序" },
        { name: "模型代码" },
        { name: "模型名称" },
        { name: "模型版本" },
        { name: "适用范围" },
        { name: "状态" },
        { name: "备注" },
        { name: "更新时间" },
        { name: "更新人员" },
        { name: "操作" },
      ],
      tableBody: [
      ],
      tableHeader2: [
        { name: "编号" },
        { name: "指标项" },
        { name: "计算公式" },
        { name: "权重（%）" },
        { name: "状态" },
        { name: "更新时间" },
        { name: "更新人员" },
        { name: "操作" },
      ],
      tableBody2: [
      ]
    };
  },
  methods:{
    /**
     * name：改变用户停用状态
     */
    changeUseSts(item, index){
        this.stopBubb = true;
        let url = this.typeNm == "业务"?`/model/busEvalModel/updateModelState/${item.id}`:`/model/proEvalModel/update/${item.batchId}/${item.flag}`;
        this.axios
          .get(url)
          .then(response => {
            if (response.code == "200") {
                this.$Message.success("更新成功");
                this.stopBubb = false;     
            }
          })
          .catch(error => {
              this.tableBody[index].flag = this.tableBody[index].flag == "0"?"1":"0";
              this.stopBubb = false;
              this.$Message.error("操作失败"); 
          });
    },
     exportFn(){
      let suffix = "_old";
      let modelCode = "";
      let modelName = "";
      this.tableBody.forEach(element => {
        if (element.id == this.id){
            modelCode = element.modelCode;
            modelName = element.modelName;
        }
      });
      if (modelCode == "NEW_RISK") suffix = "_new"
      let type = this.typeNm == "业务"?"bus"+suffix:"pro"
      let url = `/excelTooler/doExportExcel/${this.id}/${modelName}/${type}`
      this.axios
        .get(url,{responseType: "blob"})
        .then(response => {
          this.download(response,modelName+'.xls');
        })
        .catch(error => {});
    },
    importFn() {
      let suffix = "_old";
      let modelCode = "";
      this.modal1 = true;
      let batchId = "";
      let modelId = this.id;
      let modelName = "";
      this.tableBody.forEach(element => {
        if (element.id == this.id){
            modelName = element.modelName;
            batchId = element.batchId;
            modelCode = element.modelCode;
        }
      });
      if (modelCode == "NEW_RISK") suffix = "_new"
      let type = this.typeNm == "业务"?"bus"+suffix:"pro";
      this.uploadParams.type = type;
      this.uploadParams.batchId = batchId;
      this.uploadParams.modelId = modelId;
      this.uploadParams.modelName = modelName;
    },
    handleSuccess(response, file) {
      if (response.data == true) {
          this.$Message.success("上传成功");
          if (this.$route.params.type == 1) {
            this.axios
            .get(this.selectTable2Url+this.id)
            .then(response2 => {
              if (response2.code == "200") {
                  this.tableBody2 = response2.data;           
              }
            })
            .catch(error => {}); 
        }else {
            let batchId = "";
            this.tableBody.forEach(element => {
              if (element.id == this.id){
                  batchId = element.batchId;
              }
            });
            let data = {
              modelId: this.id,
              batchId: batchId,
              indicatorId: '1'
            }
            this.axios
            .post(this.selectTable2Url,JSON.stringify(data), {
              headers: { "Content-Type": "application/json" },
            })
            .then(response2 => {
              if (response2.code == "200") {
                  this.tableBody2 = response2.data;           
              }
            })
            .catch(error => {}); 
        }
      }else {
        this.$Message.error("上传失败");
        const fileList = this.$refs.upload.fileList;
        this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
      }
    },
    handleError() {
      this.$Message.error("上传失败");
    },
    handleFormatError (file) {
        this.$Notice.warning({
            title: '附件格式错误',
            desc: '请选择xls、xlsx'
        });
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    changeUseSts2(item, index) {
        let url = this.typeNm == "业务"?`/model/busEvalIndicator/updateIndStatus/`:`/model/proEvalIndicator/updateStatue`;
        if (this.typeNm == "业务"){
              this.axios
              .get(url+item.id+'/'+item.flag)
              .then(response => {
                if (response.code == "200") {
                    this.$Message.success("更新成功");
                    this.axios
                    .get(this.selectTable2Url+this.id)
                    .then(response => {
                      if (response.code == "200") {
                          this.tableBody2 = response.data;           
                      }
                    })
                    .catch(error => {});    
                }
              })
              .catch(error => {
                  this.tableBody2[index].flag = this.tableBody2[index].flag == "0"?"1":"0";
                  this.$Message.error("操作失败"); 
              });
        }else {
            let data = {
              id: item.id,
              batchId: item.batchId,
              flag: item.flag
            }
            this.axios
            .post(url, JSON.stringify(data), {
              headers: { "Content-Type": "application/json" },
            })
            .then(response => {
                if (response.code == "200") {
                    this.$Message.success("更新成功");
                    let data2 = {
                      modelId: this.id,
                      batchId: item.batchId,
                      indicatorId: '1'
                    }
                    this.axios
                    .post(this.selectTable2Url,JSON.stringify(data2), {
                      headers: { "Content-Type": "application/json" },
                    })
                    .then(response2 => {
                      if (response2.code == "200") {
                          this.tableBody2 = response2.data;           
                      }
                    })
                    .catch(error => {}); 
                }
            })
            .catch(function(error) {
                  this.tableBody2[index].flag = this.tableBody2[index].flag == "0"?"1":"0";
                  this.$Message.error("操作失败"); 
            });
        }
    },
    cancel() {},
    updateTableBody2(item){
        if (this.stopBubb)
          return;
        this.id = item.id;
        if (this.$route.params.type == 1) {
            this.axios
            .get(this.selectTable2Url+item.id)
            .then(response2 => {
              if (response2.code == "200") {
                  this.tableBody2 = response2.data;           
              }
            })
            .catch(error => {}); 
        }else {
            let data = {
              modelId: item.id,
              batchId: item.batchId,
              indicatorId: '1'
            }
            this.axios
            .post(this.selectTable2Url,JSON.stringify(data), {
              headers: { "Content-Type": "application/json" },
            })
            .then(response2 => {
              if (response2.code == "200") {
                  this.tableBody2 = response2.data;           
              }
            })
            .catch(error => {}); 
        }
    },
    operate(type, item){
      switch (type) {
        case '1':
          this.modelTitle = '修改'
          this.tabview = 'rationModelModify';
          this.showAddModal = true;
          this.dataObj = {
            typeNm:this.typeNm,
            data:item
          };
          break;
        case '2':
          if (item.flag != '1') {
              this.$Message.warning({
                content:"发布状态未启用的评级模型，无法生成模型版本！",
                duration: 2.5
                });
              return;
          }
          let url = this.typeNm == "业务"?`/model/busEvalModel/publishModel/${item.id}`:`/model/proEvalModel//publishVersion/${item.batchId}`
          this.$Modal.confirm({
              title:'提示',
              content: "确定生成模型版本么？温馨提示：确保模型规则引擎为最新，最好重启发布状态？",
              okText: "确认",
              cancelText: "取消",
              onOk: () => {
                  this.axios
                  .post(url)
                  .then(response => {
                    if (response.code == "200") {
                        this.$Message.success("操作成功");
                        item.version = Number(item.version)+1;          
                    }
                  })
                  .catch(error => {});
              },
              onCancel: () => {
              }
          });
          break;
        case '3':
          this.modelTitle = '还原历史版本'
          this.tabview = 'historyVersion';
          this.dataObj = {
            typeNm:this.typeNm,
            data:item
          };
          this.showAddModal = true;
          break;
        case '4':
          this.modelTitle = '有效性分析'
          this.tabview = 'tab2Analyze';
          this.showAddModal = true;
          break;
        case '5':
          this.dataObj = {
            typeNm:this.typeNm,
            data:item,
            noOpe: false
          };
          this.modelTitle = '模型因子'
          this.tabview = 'tab2Detail';
          this.showAddModal = true;
          break;
        case '6':
          this.modelTitle = '新增'
          this.tabview = 'tab2Add';
          this.showAddModal = true;
          let modelCode = "";
          let batchId = "";
          this.tableBody.forEach(element => {
            if (element.id == this.id){
                modelCode = element.modelCode;
                batchId = element.batchId;
            }
          });
          this.dataObj = {
            typeNm:this.typeNm,
            modelCode: modelCode,
            batchId: batchId,
            id: this.id
          };
          break;
        case '7':
          this.modelTitle = '修改模型指标';
          this.tabview = 'tab2Add';
          this.showAddModal = true;
          this.dataObj = {
            typeNm:this.typeNm,
            data:item,
          };
          break;
        case '8':
          this.$Modal.confirm({
              title:'提示',
              content: "是否删除？",
              okText: "确认",
              cancelText: "取消",
              onOk: () => {
              },
              onCancel: () => {
              }
          });
          break;
      }
    },
    saveDatas(){
      this.showAddModal = false;
      let batchId = "";
      this.tableBody.forEach(element => {
          if (element.id == this.id)
              batchId = element.batchId;
      });
      this.axios
          .get(this.selectTable1Url)
          .then(response => {
            if (response.code == "200") {
                this.tableBody = response.data;
                if (this.$route.params.type == 1) {
                    this.axios
                    .get(this.selectTable2Url+this.id)
                    .then(response2 => {
                      if (response2.code == "200") {
                          this.tableBody2 = response2.data;           
                      }
                    })
                    .catch(error => {}); 
                }else {
                    let data = {
                      modelId: this.id,
                      batchId: batchId,
                      indicatorId: '1'
                    }
                    this.axios
                    .post(this.selectTable2Url,JSON.stringify(data), {
                      headers: { "Content-Type": "application/json" },
                    })
                    .then(response2 => {
                      if (response2.code == "200") {
                          this.tableBody2 = response2.data;           
                      }
                    })
                    .catch(error => {}); 
                }
            }
          })
          .catch(error => {});
    },
    cancelDialog(val) {
      this.showAddModal = val;
    },
  },
  watch: {
    '$route':{
      handler(to, form){
        if (this.$route.params.type == 1) {
            this.typeNm = "业务";
            this.selectTable1Url = "/model/busEvalModel/getBusModelList";
            this.selectTable2Url = "/model/busEvalIndicator/getIndInfo/";
        }else {
            this.typeNm = "产品";
            this.selectTable1Url = "/model/proEvalModel/list";
            this.selectTable2Url = "/model/proEvalIndicator/list";
        }
        this.axios
          .get(this.selectTable1Url)
          .then(response => {
            if (response.code == "200") {
                this.tableBody = response.data;
                this.id = this.tableBody[0].id;
                if (this.$route.params.type == 1) {
                    this.axios
                    .get(this.selectTable2Url+this.tableBody[0].id)
                    .then(response2 => {
                      if (response2.code == "200") {
                          this.tableBody2 = response2.data;           
                      }
                    })
                    .catch(error => {}); 
                }else {
                    let data = {
                      modelId: this.tableBody[0].id,
                      batchId: this.tableBody[0].batchId,
                      indicatorId: '1'
                    }
                    this.axios
                    .post(this.selectTable2Url,JSON.stringify(data), {
                      headers: { "Content-Type": "application/json" },
                    })
                    .then(response2 => {
                      if (response2.code == "200") {
                          this.tableBody2 = response2.data;           
                      }
                    })
                    .catch(error => {}); 
                }
            }
          })
          .catch(error => {});
      },
      immediate:true
    },
    modal1: function(newValue, oldValue) {
      if (newValue == false)
          this.$refs.upload.clearFiles();
    },
  }
};
</script>
<style lang="scss" scoped>
.tableBox tbody>tr td:nth-child(8){
  min-width: 100px;
}
.ration-model-set {
  .mt_20{
    margin-top: 20px;
  }
  .bgColor{
    background: #DEF1FF;
  }
}
</style>


