<template>
  <div class="tab2-detail">
    <div class="button-content1">
        <Button type="primary" @click="operate('1')" v-if="!noOpe">
            <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
        <!-- <Button type="primary" v-if="!noOpe">
            <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button> -->
    </div>
    <div class="contentCellBody">
      <Row>
        <table class="tableBox">
          <thead>
            <tr>
              <td v-for="(item,index) in tableHeader" v-if="index != 1 || !noOpe">{{item.name}}</td>
            </tr>
          </thead>
          <tbody>
            <template v-for="(item,index) in tableBody">
              <tr>
                <td>{{index+1}}</td>
                <td v-if="!noOpe">
                  <span class="spanBox" @click="operate('2', item)">修改</span>
                  <span class="spanBox" @click="operate('3', item)">删除</span>
                </td>
                <td>{{item.enums}}</td>
                <td>{{item.minValue}}</td>
                <td>{{item.maxValue}}</td>
                <td>{{item.score}}</td>
                <td>{{item.qualScore}}</td>
                <td>{{item.remark}}</td>
                <td>
                  <i-switch :value="item.flag" true-value="1" false-value="0" disabled>
                    <span slot="open">启用</span>
                    <span slot="close">停用</span>
                  </i-switch>
                </td>
              </tr>
            </template>
          </tbody>
        </table>
      </Row>
    </div>
    <Modal :mask-closable="false"  
      v-model="showAddModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '80%'}"
    >
      <component
        :is="tabview"
        @cancel="cancelDialog"
        @save="saveDatas"
        v-if="showAddModal"
        :dataObj="dataObjj"
        width="80%"
      ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addModelFactor from './addModelFactor'
export default {
  props: ["dataObj"],
  created() {
      if (this.dataObj.typeNm == "业务"){
          if (this.dataObj.noOpe)
            this.url = `/model/busEvalIndHis/getOptionHisById/${this.dataObj.data.id}/${this.dataObj.data.modelVersion}`;
          else this.url = `/model/busEvalOption/getIndOption/${this.dataObj.data.id}`;
          this.axios
          .get(this.url)
          .then(response => {
            if (response.code == "200") {
                this.tableBody = response.data;           
            }
          })
          .catch(error => {}); 
      }else {
          if (this.dataObj.noOpe)
            this.url = `/model/proEvalOption/his/list`;
          else this.url = `/model/proEvalOption/list`;
          let data = {
            batchId: this.dataObj.data.batchId,
            indicatorId: this.dataObj.data.id
          }
          this.axios
          .post(this.url,JSON.stringify(data), {
            headers: { "Content-Type": "application/json" },
          })
          .then(response2 => {
            if (response2.code == "200") {
                this.tableBody = response2.data;           
            }
          })
          .catch(error => {}); 
      }
      this.noOpe = this.dataObj.noOpe;
  },
  data() {
    return {
      tabview: "",
      noOpe: false,
      url: "",
      modelTitle: "新增",
      showAddModal:false,
      dataObjj: {
        data: {}
      },
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "枚举项目" },
        { name: "值下限" },
        { name: "值上限" },
        { name: "对应分值" },
        { name: "定性分值" },
        { name: "备注" },
        { name: "状态" }
      ],
      tableBody: [
        {
          id: "12",
          code: "111",
          name: "低",
          roleDescribe: "0",
          Remarks: "50"
        }
      ]
    };
  },
  components:{
    addModelFactor
  },
  methods: {
    saveDatas() {
      this.showAddModal = false;
      if (this.dataObj.typeNm == "业务"){
          this.axios
          .get(this.url)
          .then(response => {
            if (response.code == "200") {
                this.tableBody = response.data;           
            }
          })
          .catch(error => {}); 
      }else {
          let data = {
            batchId: this.dataObj.data.batchId,
            indicatorId: this.dataObj.data.id
          }
          this.axios
          .post(this.url,JSON.stringify(data), {
            headers: { "Content-Type": "application/json" },
          })
          .then(response2 => {
            if (response2.code == "200") {
                this.tableBody = response2.data;           
            }
          })
          .catch(error => {}); 
      }
    },
    cancelDialog(val) {
      this.showAddModal = val;
    },
    operate(type, item) {
      switch (type) {
        case "1":
          this.showAddModal = true;
          this.modelTitle = '新增';
          this.dataObjj = {
            typeNm: this.dataObj.typeNm,
            indicatorId: this.dataObj.data.id,
            batchId: this.dataObj.data.batchId
          }
          this.tabview = 'addModelFactor';
          break;
        case "2":
          this.showAddModal = true;
          this.modelTitle = '修改';
          this.dataObjj = {
            typeNm: this.dataObj.typeNm,
            data: item
          }
          this.tabview = 'addModelFactor';
          break;
        case "3":
        let deleteUrl = this.dataObj.typeNm == "业务"?`/model/busEvalOption/deleteBusOption/${item.optId}`:`/model/proEvalOption/option/delete`;
        this.$Modal.confirm({
              title:'提示',
              content: "确认删除？删除后不可恢复！",
              okText: "确认",
              cancelText: "取消",
              onOk: () => {
                if (this.dataObj.typeNm == "业务"){
                    this.axios
                    .get(deleteUrl)
                    .then(response => {
                      if (response.code == "200") {
                          this.$Message.success("操作成功");
                          this.axios
                          .get(this.url)
                          .then(response => {
                            if (response.code == "200") {
                                this.tableBody = response.data;           
                            }
                          })
                          .catch(error => {});             
                      }
                    })
                    .catch(error => {});
                }else{
                      let data = {
                        batchId: item.batchId,
                        indicatorId: item.indicatorId,
                        optId: item.optId
                      }
                      this.axios
                      .post(deleteUrl,JSON.stringify(data), {
                        headers: { "Content-Type": "application/json" },
                      })
                      .then(response2 => {
                        if (response2.code == "200") {
                              this.$Message.success("操作成功");
                              let data2 = {
                                  batchId: this.dataObj.data.batchId,
                                  indicatorId: this.dataObj.data.id
                                }
                                this.axios
                                .post(this.url,JSON.stringify(data2), {
                                  headers: { "Content-Type": "application/json" },
                                })
                                .then(response3 => {
                                  if (response3.code == "200") {
                                      this.tableBody = response3.data;           
                                  }
                                })
                                .catch(error => {});   
                        }
                      })
                      .catch(error => {}); 
                }
              },
              onCancel: () => {
              }
          });


          break;
      }
    },
    cancel(){}
  }
};
</script>
<style scoped>
</style>


